.login-details{padding:15px 0}.login-details .form-group label{color:#00abff}.login-details .cta-login{display:flex;align-items:center;justify-content:center;margin-top:30px}.login-details .cta-login button{padding:8px 50px}

.signup-details{padding:15px 0}.signup-details .form-group label{color:#00abff}.signup-details .cta-register{display:flex;align-items:center;justify-content:center;margin-top:30px}.signup-details .cta-register button{padding:8px 50px}

.login-page{display:flex;flex-direction:row;margin:50px}.login-page .welcome-text{flex:1.8 1;margin:140px 0 0 0}.login-page .welcome-text .logo{display:flex}.login-page .welcome-text .logo img{width:109px;height:109px}.login-page .welcome-text .logo h2{padding:34px 10px 0 20px;color:#2d2d2d;font-size:34px}.login-page .welcome-text .extra-donuts{position:absolute;bottom:0;left:0}.login-page .welcome-text .extra-donuts img{height:109px}.login-page .welcome-text h1{color:#000000;font-size:40px;font-weight:bold}.login-page .welcome-text p{color:#90949c;font-size:20px;font-weight:normal}.login-page .user-details-login{flex:1 1}.login-page .user-details-login .user-data{padding:30px;box-shadow:0 8px 20px 0 rgba(0,0,0,0.1);min-height:380px;height:auto}@media (min-width: 320px) and (max-width: 640px){.login-page{flex-direction:column;margin:20px}.login-page .welcome-text{margin:20px 0 0 0}.login-page .welcome-text .extra-donuts{display:none}}

.dashboard{display:flex;min-height:100vh;height:auto;font-family:Muli, sans-serif}.dashboard .navigation{flex:0.5 1;border-right:solid 1px #dfe9f1}.dashboard .news{flex:2 1}.dashboard .news .notify-user{display:flex;padding:20px 0px 20px 30px}.dashboard .promotions{flex:1 1;padding:20px 12px 20px 0px}

.navigation .list-group .list-group-item{cursor:pointer;border:none}.navigation .list-group .list-group-item img{width:34px}.navigation .list-group .list-group-item span{font-weight:bold;font-size:25px;vertical-align:middle}

.upcoming-events{flex:1 1;height:250px;overflow:auto}.upcoming-events .table thead th{text-align:center}

.notifications{flex:1 1;height:250px;overflow:auto}.notifications .table thead th{text-align:center}

.portfolio{display:flex;text-align:center;min-height:198px;height:198px}.portfolio .items-list{flex:0.8 1;border:solid 1px #dfe9f1}.portfolio .items-list .item{padding:12px}.portfolio .items-list .item h1{font-size:26px}.portfolio .items-list .item h3{font-size:15px}

.news-feed{padding:0 20px}.news-feed .post-article{height:50px;border:solid 1px #dfe9f1}.news-feed .post-article .article{display:flex}.news-feed .post-article .article input{flex:1 1;border:none;padding:12px}.news-feed .post-article .cta{padding:5px 8px}.news-feed .post-article .cta button{margin:0 3px}.news-feed .post-article .categories{display:flex;margin:20px auto;justify-content:center}.news-feed .post-article .categories .category-type{padding:4px 36px;border:solid 1px #007bff;border-radius:100px;color:#007bff;margin:0 5px;cursor:pointer}.news-feed .post-article .categories .category-type.active{background:#007bff;color:white}.news-feed .post-article .article-posts{display:flex}.news-feed .post-article .article-posts .individual-post{border:solid 1px #dfe9f1;margin:0 10px;padding:8px}.news-feed .post-article .article-posts .individual-post .user-info{display:flex}.news-feed .post-article .article-posts .individual-post .user-info .img-desc h2{font-size:16px;padding:0 10px;font-weight:bold;margin:0}.news-feed .post-article .article-posts .individual-post .user-info .img-desc p{font-size:12px;margin:0;padding:0 10px}.news-feed .post-article .article-posts .individual-post .post-details{font-size:14px;padding:10px 0}

.updates{min-height:300px;height:400px;margin:20px 0;overflow:auto}.updates h1{font-size:26px;text-align:center;font-weight:bold}.updates .org-updates{min-height:300px;height:auto;border:solid 1px #dfe9f1}.updates .org-updates .notification{display:flex;border-bottom:solid 1px #dfe9f1}.updates .org-updates .notification .image{padding:14px 14px}.updates .org-updates .notification .img-desc h2{font-size:22px;font-weight:bold}.updates .org-updates .notification .img-desc p{font-size:16px;margin:0}

.pinned-posts{display:flex;min-height:100vh;height:auto;font-family:Muli, sans-serif}.pinned-posts .navigation{flex:0.5 1;border-right:solid 1px #dfe9f1}.pinned-posts .news{flex:2 1}.pinned-posts .news .notify-user{display:flex;padding:20px 0px 20px 30px}.pinned-posts .promotions{flex:1 1;padding:20px 12px 20px 0px}

.posts{display:flex;flex-direction:column}.posts h1{font-size:26px;font-weight:bold;margin:0;padding:44px 0 0 27px}.posts .pinned-posts{min-height:30px;height:auto;display:flex;flex-direction:column}.posts .pinned-posts .categories{display:flex;margin:10px 0px 10px 20px}.posts .pinned-posts .categories .category-type{padding:4px 36px;border:solid 1px #007bff;border-radius:100px;color:#007bff;margin:0 5px;cursor:pointer}.posts .pinned-posts .categories .category-type.active{background:#007bff;color:white}.posts .pinned-posts .article-posts{display:flex;margin:20px}.posts .pinned-posts .article-posts .individual-post{border:solid 1px #dfe9f1;margin:0 10px;padding:8px}.posts .pinned-posts .article-posts .individual-post .user-info{display:flex}.posts .pinned-posts .article-posts .individual-post .user-info .img-desc h2{font-size:16px;padding:0 10px;font-weight:bold;margin:0}.posts .pinned-posts .article-posts .individual-post .user-info .img-desc p{font-size:12px;margin:0;padding:0 10px}.posts .pinned-posts .article-posts .individual-post .post-details{font-size:14px;padding:10px 0}

.profile{display:flex;min-height:100vh;height:auto;font-family:Muli, sans-serif}.profile .navigation{flex:0.5 1;border-right:solid 1px #dfe9f1}.profile .news{flex:3 1}.profile .news .notify-user{display:flex;padding:20px 0px 20px 30px}.profile .news .posts-profile{width:90.5%}

.user-details{display:flex;flex-direction:row;border:solid 1px #dfe9f1;min-height:198px;height:198px}.user-details .user-image{flex:1 1;text-align:center;margin:35px 0}.user-details .user-data{flex:1 1;padding:10px 14px}.user-details .user-data h1{color:#0069d9;font-size:20px;font-weight:bold;margin:0;padding:0}.user-details .user-data .profession{color:#748391;font-size:14px;margin:4px 0;padding:0}.user-details .user-data .place{color:#2c3e50;font-size:12px;margin:4px 0;padding:0}.user-details .user-data .desc{color:#5d5d5d;font-size:14px;margin:0;padding:0}.user-details .user-data .social-icons .btn-primary{margin:5px 7px 0 0;padding:8px;font-size:14px}

.orginization{display:flex;min-height:100vh;height:auto;font-family:Muli, sans-serif}.orginization .navigation{flex:0.5 1;border-right:solid 1px #dfe9f1}.orginization .news{flex:3 1}.orginization .news .notify-user{display:flex;padding:20px 0px 20px 30px}.orginization .news .posts-profile{width:90.5%}

.org-details{display:flex;flex-direction:row;border:solid 1px #dfe9f1;min-height:198px;height:198px}.org-details .org-image{flex:0.5 1;text-align:center;margin:35px 0}.org-details .org-data{flex:1 1;padding:10px 14px}.org-details .org-data h1{color:#0069d9;font-size:20px;font-weight:bold;margin:0;padding:0}.org-details .org-data .profession{color:#748391;font-size:14px;margin:4px 0;padding:0}.org-details .org-data .place{color:#2c3e50;font-size:12px;margin:4px 0;padding:0}.org-details .org-data .desc{color:#5d5d5d;font-size:14px;margin:0;padding:0}.org-details .org-data .social-icons .btn-primary{margin:5px 7px 0 0;padding:8px;font-size:14px}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

